<template>
  <div class="main dataSummary quality-info">
    <div class="main-search">
      <div class="search-item">
        <span class="item-left">日期</span>
        <span class="item-right">
          <el-date-picker
            v-model="search.dateline"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择日期"
            @change="getTime">
          </el-date-picker>
        </span>
      </div>
      <div class="search-item">
        <span class="item-left">品牌</span>
        <span class="item-right">
          <el-select v-model="search.proj" multiple collapse-tags @change="changeStaff()">
            <template v-for="item in projData">
              <el-option :label="item.memo" :value="item.id"  :key="item.id">{{item.memo}}</el-option>
            </template>
          </el-select>
        </span>
      </div>
      <button type="button" class="btn-search" @click="search_data">查询</button>
    </div>
    <div class="main-table">
      <div class="table-msg">
        <div class="msg-left">
          <p>查询符合上述条件的数据<span>{{list.totalCount}}</span>条</p>
        </div>
        <div class="msg-right">
          <el-button type="button" class="btn-small" @click="operationDownload">下载报表</el-button>
        </div>
      </div>
      <el-table
        :data="list.data"
        style="width: 100%"
        border
        :default-sort = "{prop: 'date', order: 'descending'}">
        <el-table-column
          prop="dateline"
          align="center"
          label="日期"
          :formatter="formatDate"
          width="100">
        </el-table-column>
        <el-table-column
          prop="porj"
          align="center"
          label="品牌"
          :formatter="formatProj"
          width="100">
        </el-table-column>
        <el-table-column
          prop="leads"
          align="center"
          label="成单数"
          width="100">
        </el-table-column>
        <el-table-column
          prop="seats"
          align="center"
          label="坐席数"
          width="100">
        </el-table-column>
        <el-table-column
          prop="goal"
          align="center"
          label="目标"
          width="100">
        </el-table-column>
        <el-table-column
          prop="sumleads"
          align="center"
          label="总成功数"
          width="100">
        </el-table-column>
        <el-table-column
          prop="schedule"
          align="center"
          label="进度落后"
          width="100">
        </el-table-column>
        <el-table-column
          prop="percapita"
          align="center"
          label="人均单量"
          width="100">
        </el-table-column>
        <el-table-column
          prop="srate"
          align="center"
          label="整体成功率"
          width="100">
        </el-table-column>
        <el-table-column
          prop="measures"
          align="center"
          label="当日复盘结论举措"
          width="140">
        </el-table-column>
        <el-table-column
          prop="contrast"
          align="center"
          label="数据对比"
          width="280">
        </el-table-column>
        <el-table-column
          align="center"
          label="操作"
          width="80">
          <template slot-scope="scope">
            <a href="javascript:void(0)" @click="edit(scope,scope.row)">修改</a>
          </template>
        </el-table-column>
      </el-table>
      <page-table show_size="true" v-bind:cur="search.page" v-bind:total_page="list.totalPage" v-bind:total_count="list.totalCount" @change="change_data"></page-table>
    </div>
    <el-dialog class="dialog-box" :visible.sync="dialog_edit">
      <div class="main-edit">
        <div class="edit-box">
          <div class="info-item">
            <span class="item-left">目标</span>
            <span class="item-right">
              <input type="text" class="txt-common" v-model="editContent.goal" placeholder="请输入目标"></input>
              <span class="msg">请填写目标</span>
            </span>
          </div>
          <div class="edit-info">
            <div class="info-item">
              <span class="item-left">当日复盘结论举措</span>
              <span class="item-right">
                <el-input type="textarea" :rows="4" maxlength="500" v-model="editContent.measures" placeholder="请输入目标" style="width: 372px; margin-bottom: 15px;"></el-input>
                <span class="el-input__count">0/500</span>
              </span>
            </div>
          </div>
          <div class="edit-info">
            <div class="info-item">
              <span class="item-left">当月总工作日</span>
              <span class="item-right">
                <input type="number" class="txt-common" v-model="editContent.sumday" placeholder="请输入目标"></input>
              </span>
            </div>
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="save">保存</el-button>
        <el-button @click="dialog_edit=false">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {format} from '@/config/util'
  import {operationEdit,operation} from '@/api/index'
  import pageTable from '@/components/pager'
  import ajax from '@/config/ajax'
  import Qs from 'qs'
  export default {
    components:{pageTable},
    data(){
      return{                   //数据集
        search:{
          dateline: new Date(),       //开始日期
          proj: [],       //品牌
          size:20,            //每页数量
          page:1              //页码
        },
        list:{
          data:[],            //数据集
          totalPage:0,        //总页数
          totalCount:0        //总条数
        },
        editContent:{
          goal: '',
          measures: '',
          sumday: ''
        },
        dialog_edit:false,                //弹框显示
        isProjContainAll: true,     //品牌是否选则了全选
        projData: [],          //品牌字典数组
        editData: ''
      }
    },
    created() {
      this.projData = JSON.parse(localStorage.getItem("projid"));
      this.search.proj[0] = this.projData[0].id;
      this.getInitialList();
    },
    methods:{
      operationDownload() {
        var params = {
          dateline: format(this.search.dateline, 'yyyyMMdd'),
          proj: this.search.proj.join(",")
        }
        return ajax.request({
          method: 'post',
          url: 'operation/download',
          responseType: 'arraybuffer',
          transformRequest: [function (params) {      //在请求之前对data传参进行格式转换
            params = Qs.stringify(params)
            return params
          }],
          data: params
        }).then((res) => {
          let fileDownload = require("js-file-download");
          fileDownload(res,"统计报表-内部运营"+format(new Date(), "yyyy-MM-dd hh-mm-ss")+".xlsx");
        });
      },
      getInitialList(){
        operation(this.search.dateline, this.search.proj.join(","), this.search.page, this.search.size).then((res)=>{
          this.list.data=res.data.list;
          this.list.totalCount=res.data.totalCount;
          this.list.totalPage=res.data.totalPage;
        }).catch((error)=>{
          console.log(error)
        })
      },
      // 查询
      search_data () {
        if(this.search.dateline==null){
          this.$message({
            message: '请选择日期',
            type: 'warning'
          });
          return
        }
        if(this.search.proj.length == 0){
          this.$message({
            message: '请选择品牌',
            type: 'warning'
          });
          return
        }
        this.search.page = 1;
        this.getInitialList()
      },
      //格式化日期
      formatDate(row){
        var calldate = row.dateline.toString();
        return row.dateline = calldate.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3");
      },
      //品牌及外呼团队多选
      changeStaff(){
        if(this.isProjContainAll){
          this.isProjContainAll = false;   // 如果之前选中了全部，则变化后肯定不包含全部了
          this.search.proj.splice(0, 1);// 则删除第一个全部
        }else{
          this.isProjContainAll = this.search.proj.some(value => value === 0)
          if(this.isProjContainAll){
            this.search.proj = [0];
          }else{
            if(this.search.proj.length == this.projData.length-1){
              this.search.proj = [0];
              this.isProjContainAll = true;
            }
          }
        }
      },
      //处理品牌的数据
      formatProj(row){
        var projdata = this.projData;
        projdata = projdata.slice(1);
        for(var i=0;i<projdata.length;i++){
          if(row.proj == projdata[i].id){
            return row.porj = projdata[i].memo;
          }
        }
      },
      edit(index, row){
        this.dialog_edit = true;
        this.editData = row;
      },
      save(){
        this.dialog_edit = false;
        operationEdit(new Date(this.editData.dateline), this.editData.proj, this.editContent.goal, this.editContent.measures, this.editContent.sumday, this.editData.page, this.editData.size).then((res) => {
          if(res.code == '200'){
            this.$message({
              type: 'success',
              message: '修改成功'
            })
          }
        }).catch((error) => {
          console.log(error);
        })
      },
      //时间选择器时间
      getTime(val){
        this.search.dateline=new Date(val);
      },
      //分页
      change_data(index,size){
        this.search.page=index;
        this.search.size=size;
        this.getInitialList();
      }
    }
  }
</script>

<style scoped>
  @import "../../assets/css/pages/main.css";
  .quality-info{
    padding-left: 15px;
  }
  .dataSummary .main-search .search-item .txt-common{
    width: 150px;
  }
  .dataSummary .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width:150px;
  }
  select.txt-common{
    height:40px;
  }
  .dataSummary .main-search .search-item{
    min-width: 100px;
  }
  .dataSummary .main-search .search-item .txt-common{
    width: 150px;
  }

  .dataSummary .main-table table tbody tr td .quality,
  .dataSummary .main-table table tbody tr td .qualified,
  .dataSummary .main-table table tbody tr td .unqualified{
    display: inline-block;
    width: 80px;
    height: 24px;
    background-color: #c2c2c2;
    color: #ffffff;
    font-size: 12px;
    line-height: 24px;
    text-align: center;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
  }
  .dataSummary .main-table table tbody tr td .qualified{
    background-color: #2cdb6e;
  }
  .dataSummary .main-table table tbody tr td .unqualified{
    background-color: #de2f2f;
  }

  .dataSummary .main-table table tbody tr a.disabled{
    color: #b4b4b4;
    cursor:no-drop;
  }

  .dataSummary .edit-cross{
    width: 80%;
    text-align: center;
    margin: 0 auto;
  }

  .dataSummary .file-upload .edit-content{
    height: 300px;
  }

  .dataSummary .file-upload .sub-content{
    padding: 10px 20px;
    border:1px solid #ff6825;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    margin-bottom: 15px;
    color: #ff6825;
    font-size: 12px;
    line-height: 24px;
  }
  .dataSummary .file-upload .sub-content p{
    margin: 0;
    padding: 0;
  }
  .dataSummary .file-upload .sub-content p span{
    font-weight: 600;
    margin-right: 5px;
  }
  .el-select-dropdown__wrap ul li{
    padding-left: 20px !important;
    display: block;
  }
  .el-input__count {
    color: #909399;
    background: #FFF;
    position: relative;
    font-size: 12px;
    bottom: 12px;
    right: 47px;
  }
</style>



